<template>
  <div>
    <v-formly ref="form" v-model="formData" :meta="meta" :layout="'horizontal'">
    </v-formly>
    <div class="btns">
      <a-button type="primary" @click="setFormat">  </a-button>
      <a-button type="danger" @click="clear">  </a-button>
      <a-button type="primary" @click="submit">  </a-button>
    </div>
  </div>
</template>
<script>
export default {
  data: function () {
    return {
      meta: {
        type: "object",
        properties: {
          name: {
            title: "",
            type: "string",
            default: "kevin",
            ui: {
              showRequired: true,
            },
          },
          single: {
            title: "",
            type: "string",
            ui: {
              showRequired: true,
              component: "radio",
              options: [""],
            },
          },
          multiple: {
            title: "",
            type: "string",
            default: "",
            ui: {
              component: "radio",
              options: ["", "", ""],
            },
          },
          buttonStyle: {
            title: "",
            type: "string",
            ui: {
              component: "radio",
              options: [
                { label: "", value: "" },
                { label: "", value: "" },
                { label: "", value: "", disabled: true },
                { label: "", value: "" },
              ],
              radioType: "button",
              buttonStyle: "outline",
            },
          },
          buttonStyleSolid: {
            title: "",
            type: "string",
            default: "",
            ui: {
              component: "radio",
              options: [
                { label: "", value: "" },
                { label: "", value: "" },
                { label: "", value: "", disabled: true },
                { label: "", value: "" },
              ],
              radioType: "button",
              buttonStyle: "solid",
            },
          },
          vertical: {
            title: "",
            type: "string",
            ui: {
              component: "radio",
              options: ["", "", ""],
              direction: "vertical",
            },
          },
        },
        required: ["name", "single"],
      },
      formData: { name: "kevin zhang" },
    };
  },
  methods: {
    clear() {
      this.formData = null;
      // Below line do the same thing
      // this.$refs.form.reset(null);
    },
    async submit() {
      let valid = await this.$refs.form.validate();
      if (valid) {
        console.log(this.formData);
      }
    },
    setFormat() {
      const context = this.$refs.form.getContext("/multiple");
      if (context) {
        context.ui.options = ["", "", "", ""];
        context.value = "";
      }
    },
  },
};
</script>
<style lang="less" scoped></style>
