<template>
  <div>
    <v-formly ref="form" v-model="data" :meta="meta" layout="horizontal">
      <template v-slot:testSlot>
        <h1 style="color: green; border: 1px dashed green">slot  text</h1>
      </template>
    </v-formly>
    <div class="btns">
      <a-button type="primary" @click="printData">  </a-button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      data: {},
      meta: {
        properties: {
          name: {
            title: "",
            type: "string",
            ui: {
              showRequired: true,
            },
          },
          id1: {
            title: "title",
            description: "description",
            type: "null",
            ui: {
              component: "text",
              text: " title  description ", // string | () => string
            },
          },
          id2: {
            type: "null",
            ui: {
              component: "text",
              text: () => "() => string  text", // () => string
              offsetControl: 5,
            },
          },
          id3: {
            type: "null",
            ui: {
              component: "text",
              html: `<h1 style="color: red">HTML  text</h1>`, // v-html
              text: "default text",
              offsetControl: 5,
            },
          },
          id4: {
            type: "null",
            ui: {
              component: "text",
              slotNameOfDefault: "testSlot", // slot
              html: `<h1 style="color: red">I am Text</h1>`,
              text: "default text",
              offsetControl: 5,
            },
          },
          id5: {
            type: "null",
            ui: {
              component: "text",
              text: " title  offsetControl",
            },
          },
          id6: {
            type: "null",
            ui: {
              component: "text",
              text: " title, offsetControl = 5",
              offsetControl: 5,
            },
          },
          id7: {
            type: "null",
            ui: {
              component: "text",
              text: " title, offsetControl = 4",
              offsetControl: 4,
            },
          },
          id8: {
            type: "null",
            ui: {
              component: "text",
              text: "",
              spanControl: 24,
            },
          },
          address: {
            title: "",
            type: "string",
          },
        },
        required: ["name"],
      },
    };
  },
  methods: {
    async printData() {
      const valid = await this.$refs.form.validate();
      if (valid) {
        console.log(this.data);
      }
    },
  },
};
</script>
<style lang="less" scoped></style>
